import ModalComponent from '../components/public/loading'// vue

const Modal = {};

// Toast
Modal.install = function (Vue) {
  // Vue
  const ModalConstructor = Vue.extend(ModalComponent);
  // 
  const modal = new ModalConstructor();

  // div
  // div
  modal.$mount(document.createElement('div'));
  document.body.appendChild(modal.$el);

  // Vue
  // 
  const Show = (title) => {
    modal.title=title||'';
    modal.flag = true;
  };
  const Hidden = () => {
    modal.flag = false;
  };
  window.M.showLoading = Vue.prototype.$showLoading = Show;
  window.M.hideLoading = Vue.prototype.$hideLoading = Hidden;
};
export default Modal;
