import ModalComponent from '../components/public/map'// vue

const Modal = {};

// Toast
Modal.install = function (Vue) {
  // Vue
  const ModalConstructor = Vue.extend(ModalComponent);
  // 
  const modal = new ModalConstructor();

  // div
  // div
  modal.$mount(document.createElement('div'));
  document.body.appendChild(modal.$el);

  // Vue
  // 
  const locationPoint = (callback=()=>{}) => {
    modal.type = 'location';
    modal.callback = callback;
    modal.flag = true;
  };
  const locationAddress = (search='', callback=()=>{}) => {
    modal.type = 'address';
    modal.search = search;
    modal.callback = callback;
    modal.flag = true;
  };
  window.M.locationPoint = Vue.prototype.$locationPoint = locationPoint;
  window.M.locationAddress = Vue.prototype.$locationAddress = locationAddress;
};
export default Modal;
