import ModalComponent from '../components/public/select'// vue

const Modal = {};

// Toast
Modal.install = function (Vue) {
    // Vue
    const ModalConstructor = Vue.extend(ModalComponent);
    // 
    const modal = new ModalConstructor();

    // div
    // div
    modal.$mount(document.createElement('div'));
    document.body.appendChild(modal.$el);

    // Vue
    // 
    const show = (params) => {
        modal.list=params.data;
        modal.cancel=params.cancel||false;
        modal.callback=params.callback;
        modal.flag=true;
    };
    const hidden=()=>{
        modal.flag=false;
    };
    window.M.selectShow = Vue.prototype.$selectShow = show;
    window.M.selectHidden = Vue.prototype.$selectHidden = hidden;
};
export default Modal;
