import ModalComponent from '../components/public/attr'// vue

const Modal = {};

// Toast
Modal.install = function (Vue) {
    // Vue
    const ModalConstructor = Vue.extend(ModalComponent);
    // 
    const modal = new ModalConstructor();

    // div
    // div
    modal.$mount(document.createElement('div'));
    document.body.appendChild(modal.$el);

    // Vue
    // 
    const Show = (data) => {
        modal.data=data;
        modal.flag=true;
    };
    const Hidden = () => {
        modal.flag=false;
    };
    window.M.attrShow = Vue.prototype.$attrShow = Show;
    window.M.attrHidden = Vue.prototype.$attrHidden = Hidden;
};
export default Modal;
