import ModalComponent from '../components/public/pay'// vue

const Modal = {};

// Toast
Modal.install = function (Vue) {
    // Vue
    const ModalConstructor = Vue.extend(ModalComponent);
    // 
    const modal = new ModalConstructor();

    // div
    // div
    modal.$mount(document.createElement('div'));
    document.body.appendChild(modal.$el);

    // Vue
    // 
    const Show = (money,balance,callback) => {
        modal.money=money;
        modal.balance=balance;
        modal.callback=callback;
        modal.flag=true;
    };
    const Hidden = () => {
        modal.flag=false;
    };
    window.M.payShow = Vue.prototype.$payShow = Show;
    window.M.payHidden = Vue.prototype.$payHidden = Hidden;
};
export default Modal;
