import ModalComponent from '../components/public/sku'// vue

const Modal = {};

// Toast
Modal.install = function (Vue) {
    // Vue
    const ModalConstructor = Vue.extend(ModalComponent);
    // 
    const modal = new ModalConstructor();

    // div
    // div
    modal.$mount(document.createElement('div'));
    document.body.appendChild(modal.$el);

    // Vue
    // 
    const skuShow = (data,callback) => {
        modal.data=data;
        modal.callback=callback;
        modal.flag=true;
    };
    const skuHidden = () => {
        modal.flag=false;
    };
    window.M.skuShow = Vue.prototype.$skuShow = skuShow;
    window.M.skuHidden = Vue.prototype.$skuHidden = skuHidden;
};
export default Modal;
