import {SET_SERVICE_DATA} from './types';
import Http from "../../../tools/http";

const state = {
  serviceList: {},//
  serviceTypeList: {},//
  serviceShop: [],//
  near: [],//
  receipt: [],//
};
const getters = {};
const actions = {
  async getNearShop(context, params) {
    const data = await Http('near', params);
    context.commit(SET_SERVICE_DATA, {key: 'near', data: data.data});
  },
  //
  async getServiceTypeList(context) {
    const data = await Http('serviceType');
    context.commit(SET_SERVICE_DATA, {key: 'serviceTypeList', data: data.data});
  },
  //
  async setReceipt(context, params) {
    await context.commit(SET_SERVICE_DATA, {key: 'receipt', data: params});
    return true;
  }
};
const mutations = {
  [SET_SERVICE_DATA](state, params) {
    state[params.key] = params.data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations
}
